import React, { Component } from 'react'
import {
  Card,
  CardBody,
  FormGroup,
  Row,
  Col,
  Input,
  Form,
  Button,
  Label,
} from "reactstrap";
import PerfectScrollbar from "react-perfect-scrollbar"
import textToHtmlParser from 'html-react-parser';
// import {subRooter} from "../../../configs/vars";
import * as Feather from "react-feather";
import { connect } from 'react-redux';
import { addData, updateData, getDataById } from "../../../redux/actions/worksheet"
import { getInitialData as getAssessments, getDatabyFilter as getMyAssessments } from "../../../redux/actions/assessment"
import Checkbox from "../../../components/@vuexy/checkbox/CheckboxesVuexy"

export class EditForm extends Component { 
  static getDerivedStateFromProps(props, state) {
    if(!Object.is(props.currentSheet, state.currentData)) {
      return {
        _id: props.currentSheet ? props.currentSheet._id : "",
        name: props.currentSheet ? props.currentSheet.name : "",
        selectedAssessments: props.currentSheet ? props.currentSheet.assessments : [],
        currentData: props.currentSheet,
        topicId: props.currentSheet.topicId._id
      }
    }
    // ************************ two case admin and tutor(level 1 & level2) ******************
    /**
     * if user is logined as admin or tutor level 2.
     * get all assessment 
     *  if(!Object.is(state.assessments, props.allAssessment)
      ) {
        return {
          assessments: props.allAssessment
        }
      }
      if not, get his own assessment
      if(!Object.is(state.assessments, props.filteredAssessment)) {
          return {
            assessments: props.filteredAssessment
          }
        }
     */
    if(!Object.is(state.assessments, props.filteredAssessment)) {
      return {
        assessments: props.filteredAssessment
      }
    }
    // if(!Object.is(state.assessments, props.allAssessment)) {
    //   return {
    //     assessments: props.allAssessment
    //   }
    // }
    // ****************************************************

    return null
  }
  
	state = {
    _id: "",
		name: '',
    isInvalid: false,
    assessments: this.props.filteredAssessment || this.props.allAssessment,
    // edit
    currentData: null,
    selectedAssessments: [],
    filterOption: {},
    topicId: ""
  }
  _scrollBarRef;

  componentDidUpdate(prevProps, prevState) {
    let filterOption = this.props.filterOption;
    if(!Object.is(this.props.filterOption, this.state.filterOption)) {
      // if(filterOption && filterOption.selectedCategoryId 
      //     && filterOption.selectedLevelId && filterOption.selectedTopicId
      // ) {
        this.setState({
          filterOption: filterOption,
          topicId: filterOption.selectedTopicId
        })
      // }
    }
    if(prevState.topicId !== this.state.topicId) {
      this.getAssessments(filterOption.selectedTopicId)
    }
  }

  getAssessments = (topicId) => {
    // console.log('getAssessments');
    const auth = this.props.auth;
    let filter = {
      topic: topicId,
    }
    if(auth.userRole !== 'admin'){
      filter.createdBy = auth.userinfo.id
    }    // if(auth.userRole === 'admin') {
    //   this.props.getAssessments({
    //     topic: topicId
    //   })
    // } else {
      this.props.getMyAssessments(filter)
    // }
  }
  
	handleOnSubmit = (event) => {
    event.preventDefault();

    if (this.isValid(this.state)) {
      
			if(this.state.currentData === null) {
				this.props.addData({
          name: this.state.name,
          topicId: this.state.topicId,
          assessments: this.state.selectedAssessments
				})
			} else {
				this.props.updateData({
          _id: this.state._id,
          name: this.state.name,
          topicId: this.state.topicId,
          assessments: this.state.selectedAssessments
				})
      }
      
      this.setState({
        isInvalid: false
      })
		} else {
      this.setState({
        isInvalid: true
      })
    }
  }
  
  isValid = (state) => {
    return state.name && state.selectedAssessments.length > 0 && state.topicId !== ""
  }
  
  handleChangeAssessment = (assessment) => {
    let selectedAssessments = this.state.selectedAssessments.slice();
    if(selectedAssessments.find((elt) => elt === assessment._id)) {
      selectedAssessments = selectedAssessments.filter((id) => id !== assessment._id)
  
      this.setState({
        selectedAssessments
      })
    } else {
      selectedAssessments.push(assessment._id)
  
      this.setState({
        selectedAssessments
      })
    }
  }

	initForm = (e) => {
    e.preventDefault();
    this.setState({
      name: "",
      selectedAssessments: []
		})
  }
  
  filterbyDateAndMCQ = (filteredAssessment) => {
    let filterOption = this.props.filterOption;

    let rangePicker = filterOption.rangePicker;
    let startTime   = 0
    let endTime     = new Date().getTime();

    if( rangePicker && rangePicker[0] ) {
      startTime = new Date(rangePicker[0]).getTime()
    } else {
      startTime = 0
    }
    if( rangePicker && rangePicker[1] ) {
      endTime = new Date(rangePicker[1]).getTime()
    } else {
      endTime = new Date().getTime();
    }

    filteredAssessment = filteredAssessment.filter((item) => {
      let condition = 
        (filterOption.MCQ === "" ? true : (filterOption.MCQ === "OTHER" ? !item.isMCQ : item.isMCQ))
        && new Date(item.createdAt).getTime() > startTime && new Date(item.createdAt).getTime() < endTime

      return condition
    });
    return filteredAssessment
  }

	handleChange = (name, type) => {
		this.setState({
			[name]: type
		})
  }

	render() {
    const { 
      // description, 
      name,
      selectedAssessments,
      assessments,
      topicId
    } = this.state;
    
    let filterdAssessments = this.filterbyDateAndMCQ(assessments);

    filterdAssessments = filterdAssessments.map((elt) => {
      if(selectedAssessments.find((id) => id === elt._id)) {
        elt.isCompleted = true
      } else {
        elt.isCompleted = false
      }
      return elt
    })

    var renderItems = 
      filterdAssessments.length > 0 ? (
        filterdAssessments.map((question, i) => {
          return (
            <li
              className={`d-block todo-item ${question.isCompleted ? "completed" : ""}`}
              key={i}
              onClick={() => {
                this.handleChangeAssessment(question)
              }}
            >
              <Card>
                <CardBody className='p-1'>
                  <div className="todo-title-wrapper d-flex justify-content-between mb-50">
                    <div className="todo-title-area d-flex align-items-center">
                      <div className="title-wrapper d-flex">
                        <Checkbox
                          color="primary"
                          className="user-checkbox"
                          icon={<Feather.Check className="vx-icon" size={12} />}
                          label={""}
                          checked={question.isCompleted}
                          size="sm"
                          onClick={e => {
                            e.stopPropagation()
                          }}
                          onChange={e => e.stopPropagation()}
                        />
                        <h6 className="todo-title mt-50 mx-50">{question.question}</h6>
                      </div>
                    </div>
                  </div>
                  <div className="todo-desc truncate mb-0 ml-2" style={{whiteSpace: "nowrap", textOverflow: "ellipsis", overflow: "hidden"}}>
                  {
                    textToHtmlParser(question.description)
                  }
                  </div>
                </CardBody>
              </Card>
            </li>
          )
        })
      ) : (
        <p className="p-1 text-center mt-2 font-medium-3 text-bold-500"
          style={{height: 20, overflow: 'hidden'}}
        >
          No questions at this time
        </p>
      )
    
		return (
      <React.Fragment>
            <Form className='text-left' onSubmit={this.handleOnSubmit}  >
              <Row>
                <Col sm="12">
                  <Card>
                    <CardBody>
                      <FormGroup>
                        <Label for="name">Sheet Name</Label>
                        <Input
                          type="text"
                          name="name"
                          id="name"
                          placeholder="Input Sheet name"
                          value={name}
                          onChange={(e) => this.setState({
                            name: e.target.value
                          })}
                          invalid={name === ''}
                        />
                      </FormGroup>
                    </CardBody>
                  </Card>
                </Col>
                {/* <Col md='12' >
                  <Card>
                    <Label for="description" className='pb-1'>Description</Label>
                    <Editor id='description' html={description} onChange={(values) => this.handleEditor(values)}/>
                  </Card>
                </Col> */}
                <Col md='12' >
                  <h2 className='ml-1'>Questions</h2>
                  <div className='pt-2'>
                    <PerfectScrollbar
                      className="mt-1"
                      options={{
                        wheelPropagation: false
                      }}
                      style={{
                        maxHeight: "calc(100vh - 19.35rem)",
                        position: "relative"
                      }}
                      ref = {(ref) => { this._scrollBarRef = ref; }}
                    >
                      {
                        topicId  && <ul>{renderItems}</ul>
                      }
                    </PerfectScrollbar>
                  </div>
                </Col>
                <Col md='12' >
                  <FormGroup className='text-right m-0'>
                    {
                      this.state.isInvalid && <p className='text-danger'>Check Name and Questions</p>
                    }
                    <Button.Ripple
                      color="primary"
                      type="submit"
                      className="mr-1 mb-1"
                    >
                      Submit
                    </Button.Ripple>
                    <Button.Ripple
                      outline
                      color="warning"
                      type="reset"
                      className="mb-1"
                      onClick={this.initForm}
                    >
                      Reset
                    </Button.Ripple>
                  </FormGroup>
                </Col>
              </Row>
              {/* actions end */}
            </Form>
        </React.Fragment>
		)
	}
}
const mapStateToProps = (state) => {
	return {
    filteredAssessment: state.assessment.queryData,
    allAssessment: state.assessment.allData,
    auth: state.auth
	}
}

const mapDispatchToProps = {
  updateData,
  addData,
  getDataById,
  getAssessments,
  getMyAssessments
}

export default connect(mapStateToProps, mapDispatchToProps)(EditForm)