import React, { Component } from "react"
import {
  Button,
  UncontrolledDropdown,
  DropdownMenu,
  DropdownToggle,
  DropdownItem,
  Input
} from "reactstrap"
import DataTable from "react-data-table-component"
import ReactPaginate from "react-paginate"
import {
  Edit,
  Trash,
  ChevronDown,
  Plus,
  Check,
  ChevronLeft,
  ChevronRight,
} from "react-feather"
import { connect } from "react-redux"
import {
  getData,
  getInitialData,
  deleteData,
  filterData,
  deleteDataByIds
} from "../../../redux/actions/worksheet"
import {
  setValues
} from "../../../redux/actions/selectTopic"
import Checkbox from "../../../components/@vuexy/checkbox/CheckboxesVuexy"
import { history } from "../../../history"
import { matchYMD } from "../../../service/helper";

import "../../../assets/scss/plugins/extensions/react-paginate.scss"
import "../../../assets/scss/pages/data-list.scss";

const ACTION_TYPES = {
  DELETE: 'DELETE'
}

// const chipColors = {
//   "on hold": "warning",
//   delivered: "success",
//   pending: "primary",
//   canceled: "danger"
// }

const selectedStyle = {
  rows: {
    selectedHighlighStyle: {
      backgroundColor: "rgba(115,103,240,.05)",
      color: "#7367F0 !important",
      boxShadow: "0 0 1px 0 #7367F0 !important",
      "&:hover": {
        transform: "translateY(0px) !important"
      }
    }
  }
}

const ActionsComponent = props => {
  return (
    <div className="data-list-action">
      <Edit
        className="cursor-pointer mr-1"
        size={20}
        onClick={() => {
          return props.currentData(props.row)
        }}
      />
      <Trash
        className="cursor-pointer"
        size={20}
        onClick={() => {
          props.deleteRow(props.row)
        }}
      />
    </div>
  )
}

const CustomHeader = props => {
  return (
    <div className="data-list-header d-flex justify-content-between flex-wrap" style={{paddingTop: 4}}>
      <div className="actions-left d-flex flex-wrap">
        {/* <UncontrolledDropdown className="data-list-dropdown mr-1">
          <DropdownToggle className="p-1" color="primary">
            <span className="align-middle mr-1">Actions</span>
            <ChevronDown size={15} />
          </DropdownToggle>
          <DropdownMenu tag="div" right>
            <DropdownItem tag="a"
              onClick={() => props.handleActions(ACTION_TYPES.DELETE)}
            >Delete</DropdownItem>
          </DropdownMenu>
        </UncontrolledDropdown> */}
        <Button
          className="add-new-btn mr-1"
          color="primary"
          outline
          onClick={() => props.toggle('2', null)}
        >
          <Plus size={15} />
          <span className="align-middle">Add New</span>
        </Button>
        <Button
          color="danger"
          onClick={() => props.handleActions(ACTION_TYPES.DELETE)}
        >
          <Trash size={15} />
          <span>Delete</span>
        </Button>
      </div>
      <div className="actions-right d-flex flex-wrap mt-sm-0 mt-2">
        <UncontrolledDropdown className="data-list-rows-dropdown mr-1 d-md-block d-none">
          <DropdownToggle color="" className="sort-dropdown">
            <span className="align-middle mx-50">
              {`${props.index[0]} - ${props.index[1]} of ${props.total}`}
            </span>
            <ChevronDown size={15} />
          </DropdownToggle>
          <DropdownMenu tag="div" right>
            <DropdownItem tag="a" onClick={() => props.handleRowsPerPage(4)}>
              4
            </DropdownItem>
            <DropdownItem tag="a" onClick={() => props.handleRowsPerPage(10)}>
              10
            </DropdownItem>
            <DropdownItem tag="a" onClick={() => props.handleRowsPerPage(15)}>
              15
            </DropdownItem>
            <DropdownItem tag="a" onClick={() => props.handleRowsPerPage(20)}>
              20
            </DropdownItem>
          </DropdownMenu>
        </UncontrolledDropdown>
        <div className="filter-section">
          <Input type="text" onChange={e => props.handleFilter(e)} />
        </div>
      </div>
    </div>
  )
}

class DataListConfig extends Component {
  static getDerivedStateFromProps(props, state) {
    let newState = {};
    if (
      props.dataList.data.length !== state.data.length ||
      state.currentPage !== props.parsedFilter.page
    ) {
        newState.data = props.dataList.data;
        newState.allData = props.dataList.filteredData;
        newState.totalPages = props.dataList.totalPages;
        newState.currentPage = parseInt(props.parsedFilter.page) - 1;
        newState.rowsPerPage = parseInt(props.parsedFilter.perPage);
        newState.totalRecords = props.dataList.totalRecords;
        newState.sortIndex = props.dataList.sortIndex
    }
    if(!Object.is(props.filterOption, state.filterOption)) {
      newState.filterOption = props.filterOption
    }

    if(Object.getOwnPropertyNames(newState).length === 0) {
      return null
    }
    // Return null if the state hasn't changed
    return newState
  }

  state = {
    data: [],
    totalPages: 0,
    currentPage: 0,
    columns: [
      {
        name: "Question",
        selector: "question",
        sortable: true,
        minWidth: "250px",
        cell: row => (
          <p title={row.name} className="text-truncate text-bold-500 mb-0">
            {row.name}
          </p>
        )
      },
      {
        name: "topic",
        center: true,
        cell: row => (
          <p title={row.topicId && row.topicId.name} className="text-truncate text-bold-500 mb-0">
            {row.topicId && row.topicId.name}
          </p>
        )
      },
      {
        name: "Count",
        minWidth: "20px",
        center: true,
        cell: row => (
          <p title={row.assessments.length} className="text-truncate text-bold-500 mb-0">
            {row.assessments.length}
          </p>
        )
      },
      {
        name: "Created By",
        selector: "createdBy.name",
        sortable: true,
        center: true,
        cell: row => (
          row.createdBy ? row.createdBy.firstname + " " + row.createdBy.lastname : ""
        )
      },
      {
        name: "Created At",
        sortable: true,
        cell: row => (matchYMD(row.createdAt))
      },
      {
        name: "Actions",
        sortable: true,
        cell: row => (
          <ActionsComponent
            row={row}
            getData={this.props.getData}
            parsedFilter={this.props.parsedFilter}
            currentData={this.handleCurrentData}
            deleteRow={this.handleDelete}
          />
        )
      }
    ],
    allData: [],
    value: "",
    rowsPerPage: 4,
    sidebar: false,
    currentData: null,
    selected: [],
    totalRecords: 0,
    sortIndex: [],
    addNew: "",
    filterOption: {}
  }

  thumbView = false

  componentDidMount() {
    let params = Object.keys(this.props.parsedFilter).length
      ? this.props.parsedFilter
      : { page: 1, perPage: 4 }
    this.props.getData(params)
    this.props.getInitialData()
  }

  handleFilter = e => {
    this.setState({ value: e.target.value })
    this.props.filterData(e.target.value)
  }

  handleActions = (actionType) => {
    if(actionType === ACTION_TYPES.DELETE) {
      let selected = this.state.selected
      let ids = selected.map((elt) => (elt._id));

      if(ids.length > 0) {
        this.props.deleteDataByIds(ids);
      }
    }
  }

  handleRowsPerPage = value => {
    let { parsedFilter, getData } = this.props
    let page = parsedFilter.page !== undefined ? parsedFilter.page : 1
    history.push(`/myarea/worksheets?page=${page}&perPage=${value}`)
    this.setState({ rowsPerPage: value })
    getData({ page: parsedFilter.page, perPage: value })
  }

  handleSidebar = (boolean, addNew = false) => {
    this.setState({ sidebar: boolean })
    if (addNew === true) this.setState({ currentData: null, addNew: true })
  }

  handleDelete = row => {
    this.props.deleteData(row)
    if (this.state.data.length - 1 === 0) {
      let urlPrefix = "/myarea/worksheets";
      let params = this.getParams(this.props.parsedFilter);
      history.push(
        `${urlPrefix}?page=${parseInt(
          params.page - 1
        )}&perPage=${params.perPage}`
      )
    }
  }

  handleCurrentData = obj => {
    this.setState({ currentData: obj });
    // console.log("obj", obj);
    this.props.setValues({
      topicId: obj.topicId._id
    })
    this.props.toggle("2", obj) 
  }

  handlePagination = page => {
    let { parsedFilter, getData } = this.props
    let perPage = parsedFilter.perPage !== undefined ? parsedFilter.perPage : 4
    let urlPrefix = "/myarea/worksheets"
    history.push(
      `${urlPrefix}?page=${page.selected + 1}&perPage=${perPage}`
    )
    getData({ page: page.selected + 1, perPage: perPage })
    this.setState({ currentPage: page.selected })
  }

  getParams = (parsedFilter) => {
    let params = Object.keys(parsedFilter).length
      ? this.props.parsedFilter
      : { page: 1, perPage: 4 };

    if(params.page === undefined) {
      params.page = 1
    }
    if(params.perPage === undefined) {
      params.perPage = 4
    }
    return params;
  }

  render() {
    let {
      columns,
      data,
      allData,
      totalPages,
      value,
      rowsPerPage,
      totalRecords,
      sortIndex
    } = this.state;
    let rows = value.length ? allData : data

    return (
      <React.Fragment>

        <div
          className={`data-list ${
            this.props.thumbView ? "thumb-view" : "list-view"
          }`}>
          <DataTable
            columns={columns}
            data={rows}
            pagination
            paginationServer
            paginationComponent={() => (
              <ReactPaginate
                previousLabel={<ChevronLeft size={15} />}
                nextLabel={<ChevronRight size={15} />}
                breakLabel="..."
                breakClassName="break-me"
                pageCount={totalPages}
                containerClassName="vx-pagination separated-pagination pagination-end pagination-sm mb-0 mt-2"
                activeClassName="active"
                forcePage={
                  this.props.parsedFilter.page
                    ? parseInt(this.props.parsedFilter.page - 1)
                    : 0
                }
                onPageChange={page => this.handlePagination(page)}
              />
            )}
            noHeader
            subHeader
            selectableRows
            responsive
            pointerOnHover
            selectableRowsHighlight
            onSelectedRowsChange={data =>
              this.setState({ selected: data.selectedRows })
            }
            customStyles={selectedStyle}
            subHeaderComponent={
              <CustomHeader
                handleSidebar={this.handleSidebar}
                handleFilter={this.handleFilter}
                handleRowsPerPage={this.handleRowsPerPage}
                rowsPerPage={rowsPerPage}
                total={totalRecords}
                index={sortIndex}
                handleActions={this.handleActions}
                toggle={(item) => this.props.toggle(item)}
              />
            }
            sortIcon={<ChevronDown />}
            selectableRowsComponent={Checkbox}
            selectableRowsComponentProps={{
              color: "primary",
              icon: <Check className="vx-icon" size={12} />,
              label: "",
              size: "sm"
            }}
          />
        </div>
      </React.Fragment>
    )
  }
}

const mapStateToProps = state => {
  return {
    dataList: state.worksheet
  }
}

export default connect(mapStateToProps, {
  getData,
  deleteData,
  getInitialData,
  filterData,
  deleteDataByIds,
  setValues
})(DataListConfig)
