import React, { Component } from 'react'
import { Row, Col, TabPane, TabContent, Button } from "reactstrap"
import { ArrowLeft } from "react-feather"
import queryString from "query-string"
import ThumbViewConfig from "./DataListConfig"
import Breadcrumbs from "../../../components/@vuexy/breadCrumbs/BreadCrumb";
import SelectTopic from "../../ui-elements/SelectTopic";
import { connect } from "react-redux";
import EditForm from "./EditForm";

import { getInitialData as getLevels } from "../../../redux/actions/level";
import { getParentData as getCategories } from "../../../redux/actions/category";
import { getDataByType as getTopics } from "../../../redux/actions/topic";

export class WorkSheetListPage extends Component {
  state = {
    filterOption: {
      MCQ: "",
      rangePicker: '',
      selectedCategoryId: '', 
      selectedLevelId: '',
      selectedTopicId: '',
    },
    currentSheet: null,
    active: '1'
  }

  componentDidMount() {
    this.props.getLevels();
    this.props.getCategories();
    this.props.getTopics('ASSESSMENT');
  }

  toggle = (tab, selectedObj) => {
    if (this.state.active !== tab) {
      this.setState({ 
        active: tab,
        currentSheet: selectedObj || null
      })
    }
  }

  handleTopicChange = (value) => {
    this.setState({
      filterOption: value
    })
  }

	render() {

    const {
      levels,
      categories,
      topics,
    } = this.props;
    const { filterOption } = this.state;

		return (
			<React.Fragment>
				<Breadcrumbs
					breadCrumbTitle="WorkSheet"
					breadCrumbParent="WorkSheet"
					breadCrumbActive={`${this.state.active === "1" ? "List" : "New"}`}
				/>
				<Row>
          <Col sm="12">
            <TabContent activeTab={this.state.active}>
              <TabPane tabId="1">
                <ThumbViewConfig  
                    parsedFilter={queryString.parse(this.props.location.search)}
                  filterOption={filterOption}
                  toggle={(item, obj) => this.toggle(item, obj)}
                />
              </TabPane>
              <TabPane tabId="2">
              <div className='ml-1 mb-1'>
                <Button.Ripple outline color="info"  
                  onClick={() => this.toggle('1')}
                >
                  <ArrowLeft size={20} />	Back to List
                </Button.Ripple>
              </div>
                <SelectTopic
                  levels={levels}
                  categories={categories}
                  topics={topics}
                  onChange={(value) => this.handleTopicChange(value)}
                />
                {
                  this.state.active === "2" && <EditForm 
                    currentSheet={this.state.currentSheet}
                    filterOption={filterOption}
                    toggle={(item) => this.toggle(item)}
                  />
                }
              </TabPane>
            </TabContent>
          </Col>
        </Row>
			</React.Fragment>
		)
	}
}

const mapStateToProps = (state) => {
  return {
    levels: state.level.allData,
    categories: state.category.allData,
    topics: state.topic.dataPerType
  }
}
const mapDispatchToProps = {
  getLevels,
  getCategories,
  getTopics ,
}

export default connect(mapStateToProps, mapDispatchToProps)(WorkSheetListPage)